/* Стили для слайдера верхнего блока. -------------------------------------------------- */

.slider-main {
  position: relative;
}

.slider-main__img {
  padding-bottom: 44.335%; /* Чтобы высота слайдера при ширине экрана 1920px была как по макету - 720px. */

  border-radius: 40px;
  background-color: gray;
  overflow: hidden;
}

.slider-main__control {
  position: absolute;
  left: 0;
  bottom: -23px;
  width: 100%;

  -webkit-transform: translateY(100%);

          transform: translateY(100%);
}

/* slider.css */

/* Общие стили для слайдера. ----------------------------------------------------------- */

.slider__control {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider__arrow {
  position: relative;
  flex: 0 0 32px;
  height: 32px;
}

.slider__arrow::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-image: url(/netomerch-frontend/static/media/slider__arrow.d01d2774.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.slider__control_gray .slider__arrow::after {
  background-image: url(/netomerch-frontend/static/media/slider__arrow_gray.f0e453e0.svg);
}

.slider__arrow_right::after {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.slider__arrow:hover::after {
  opacity: 0.7;
}

.slider__control_gray .slider__arrow:hover::after {
  /* background-image: url("./icons/slider__arrow_gray-active.svg"); */
}

.slider__points {
  display: flex;
  align-items: center;
  margin: 0 64px;
}

.slider__point {
  position: relative;
  min-width: 8px; /* Размер точки. При попытке задать размер через свойство flex flex-контейнер теряет ширину. */
}

.slider__point::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  padding-bottom: 100%;

  border-radius: 50%;
  background-color: #ffffff;
  opacity: 0.5;

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);
}

.slider__control_gray .slider__point::after {
  background-color: #A5A4A7;
}

.slider__point:not(:last-child) {
  margin-right: 32px;
}

.slider__point_active::after {
  opacity: 1;
}

.slider__control_gray .slider__point_active::after {
  background-color: #4B4950;
}
